.bodyHwp{
display:flex;
flex-direction:column;
background-color:#850f55;
}

.hwp2024Header {
    display:block;
    position:absolute;
    text-align: center;
    font-size: 1.75em;
    color:white;
    font-weight: bold;
    border:solid 3px;
    width: 100vw;
    height: auto;
} 

h1 .hadrianswallTitle{
  font-size:0.5rem;
}

 #hwp2024title {
    display:block;
    text-align: center;
    margin-top:1%;
    color: 	#52555c;
}

 #backbutton{
    display:block;
    position:absolute;
    height:50px;
    width:15%;
    background-color:gray;
    border:black 2px solid;
    text-align: center;
    font-size: large;
    font-weight: bold;
    color:white;
    margin: 5% 25% 5% 75%;
    border-radius:15%;
    -webkit-border-radius:15%;
    -moz-border-radius:15%;
    -ms-border-radius:15%;
    -o-border-radius:15%;
  } 


  .backbuttonLink{
    color:white
    } 

  .LinkHWP{
    color:blueviolet;
    font-weight: bold;
  }

  .BoldTitle{
    font-weight:bold;
  }

  .datenewsDivHwp{
      color:white;
      background-color:#850f55	;
      display:flex;
      flex-direction:column;
      margin-top: 2%;
      margin-bottom:1%;
      padding:4%;
    }
    
    .dateHwp{
      color:white;;
      background-color:#66023c;
      text-align: center;
      padding:10px 10px 10px 10px;
      margin:5px;
      font-size:1.2rem;
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      font-weight: bold;
    }
  
    .newsheaderHwp{
      color:white;;
      background-color:#66023c;
      text-align: center;
      padding:1%;
      margin:5px;
      font-size:1.2rem;
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      /* font-weight:bold; */
    }
  
    .newsHwp{
      color:white;;
      /* border:solid 3px white; */
      background-color:#66023c;
      text-align: left;
      padding:10px 10px 10px 10px;
      margin:5px;
      font-size:1.2rem;
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      /* font-weight:bold; */
    }

    .blogPhotoDivHwp{
      display:flex;
      flex-direction: row;
      background-color:#66023c;
      padding-bottom: 20%;
      padding-top: 5%;
      margin-top:2%;
    }
  
    .blogPhoto img{
      display: block;
      margin-left: auto;
      margin-right: auto;
      width:70%;
      height:70%;
      border:solid grey;
      border-radius:30px;
      }

      .blogPhoto1HWP img{
        display: block;
        margin-left: auto;
        margin-right: auto;
        width:30%;
        height:auto;
        border:solid #36454F 4px;
        border-radius:30px;
        margin-top:2%;
        margin-bottom:1%;
        /* pointer-events: none; */
        }

        .HWPfigcaption {
          text-align: center;
          font-family: bold;
          color:#36454F;
          font-size:1.2rem;
          font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
          overflow-wrap:normal;
          padding:1%;
          margin-top:1%;
          margin-bottom:1%;
        }

      .slideshowentryHwp {
        display: column;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        /* pointer-events:none; */
        width:80%;
        height:auto;
        border:solid #D5544F 5px;
        border-radius:80%;
        margin-top:5%;
        margin-bottom:5%;
        -webkit-border-radius:80%;
        -moz-border-radius:80%;
        -ms-border-radius:80%;
        -o-border-radius:80%;
}    

.hwpIntroPhoto {
  margin-left: auto;
  margin-right: auto;
  pointer-events:none;
  width:35%;
  height:auto;
  border:solid #66023c 8px;
  border-radius:80%;
  margin-top:1%;
  margin-bottom:1%;
  -webkit-border-radius:80%;
  -moz-border-radius:80%;
  -ms-border-radius:80%;
  -o-border-radius:80%;
}   

