}

.titles{
  color:#66023c;
  font-size:1.3rem;
}

.headTitles{
  color:#66023c;
  font-size:1.3rem; 
  text-decoration: underline;
}

.slideshowHostPhotoHwp{
  display:flex;
  flex-direction: column;
  background-color: #66023c;
  padding-bottom: 5%;
  padding-top: 5%;
  margin-top:0;
  margin-bottom: 0;
  justify-content:center;
  text-align:center;
}

.introHostPhotoHwp{
  display:flex;
  flex-direction: column;
  background-color: #66023c;
  padding-bottom: 0;
