@media screen and (min-width:481) and (max-width:902px) {

  /*To ensure on mobile screens below 902px width (this includes one plus phones) that relevant components of 
  the site are displayed with the best dimensions for the smaller devices*/

.hwp2024header {
 display:block;
 text-align: center;
 font-size: 1.75em;
 color:white;
 font-weight: bold;
 /* border:solid 3px; */
 width: 100vw;
 /* height: 50vh; */
 height:auto;
 position:absolute; 
} 

/*To ensure on mobile screens above 480px but below 902px that photos are displayed in columnar format to ensure sizing and clarity of image optimal*/
.blogPhotoDivHwp{
 display:flex;
 flex-direction: column;
 background-color: #66023c;
 padding-bottom: 15%;
 padding-top: 5%;
 margin-top:2%;
}

.hwpIntroPhoto {
 margin-left: auto;
 margin-right: auto;
 pointer-events:none;
 width:65%;
 height:auto;
 border:solid #66023c 8px;
 border-radius:80%;
 margin-top:1%;
 margin-bottom:1%;
 -webkit-border-radius:80%;
 -moz-border-radius:80%;
 -ms-border-radius:80%;
 -o-border-radius:80%;
}   
} 

@media screen and (min-width:402px) and (max-width:900px){
  .blogPhotoDivHwp{
    display:flex;
    flex-direction: column;
    background-color: #D19B2F;
    padding-bottom: 15%;
    padding-top: 5%;
    margin-top:2%;
    transform:size(1.2);
    -webkit-transform:size(1.2);
    -moz-transform:size(1.2);
    -ms-transform:size(1.2);
    -o-transform:size(1.2);
}

